.header {
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    min-width: 30rem;
    z-index: 100;
}

.header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}

.header__logo {
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}

.header__searchIcon {
    position: relative;
    padding: 5px;
    height: 28px !important;
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    background-color: #cd9042;
    left: 3.5rem;
}

.header__searchInput {
    position: relative;
    left: 4rem;
    height: 17px;
    padding: 10px;
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    border: none;
    outline: none;
    width: 85%;
}

.header__locaationIcon {
    height: 15px !important;
    margin-right: -.9rem;
    color: white;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
}

.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}

.header__optionLineOne {
    font-size: 11px;
}

.header__optionLineTwo {
    font-size: 14px;
    font-weight: 800;
}

.header__optionBasket {
    display: flex;
    align-items: center;
    color: white;
}

/* header__optionLineTwo  */
.header__basketCount {
    margin-left: 10px;
    margin-right: 10px;

}

@media screen and (max-width:1310px){
    .header__searchInput {
        width:75%;
        margin-left: -2rem;
    }
}

@media screen and (max-width:940px) {
    .header__searchInput {
        width: 70%;
        margin-left: -2rem;
    }
}